<template>
  <!-- Main content -->
  <section class="content">
    <!-- Info boxes -->
    <div class="row">
      <div class="clearfix visible-sm-block"></div>
        <div class="col-md-3 col-sm-6 col-xs-12">
          <div class="info-box info-box-sm bg-aqua">
            <span class="info-box-icon"><i class="fa fa-university"></i></span>
            <div class="info-box-content">
             <router-link to="/financieroIndices">
                <a>
                  <span class="info-box-text">Indices</span>
                </a>
              </router-link>
            </div>
            <!-- /.info-box-content -->
          </div>
        </div>
        <!-- /.col -->
        <div class="col-md-3 col-sm-6 col-xs-12">
          <div class="info-box info-box-sm bg-yellow">
            <span class="info-box-icon"><i class="fa fa-line-chart"></i></span>
            <div class="info-box-content">
              <span class="info-box-text">Acciones</span>
            </div>
            <!-- /.info-box-content -->
          </div>
        </div>

        <div class="col-md-3 col-sm-6 col-xs-12">
          <div class="info-box info-box-sm bg-aqua">
            <span class="info-box-icon"><i class="fa fa-users"></i></span>
            <div class="info-box-content">
              <router-link to="/financieroFondos">
                <a>
                  <span class="info-box-text">Fondos</span>
                </a>
              </router-link>
            </div>
            <!-- /.info-box-content -->
          </div>
        </div>

        <div class="col-md-3 col-sm-6 col-xs-12">
          <div class="info-box info-box-sm bg-aqua">
            <span class="info-box-icon"><i class="fa fa-percent"></i></span>
            <div class="info-box-content">
              <span class="info-box-text">Ratios</span>
            </div>
            <!-- /.info-box-content -->
          </div>
        </div>
    </div>
    <!-- /.row -->

    <div class="col-xs-12">
      <div class="box">
        <div class="box-header with-border">
          <h3 class="box-title"></h3>
          <div class="box-body">

            <div class="col-sm-6 col-xs-12">
              <p class="text-center">
                <strong>Evolución del precio de la acción</strong>
              </p>
              <canvas id="trafficBar" ></canvas>
            </div>

            <hr class="visible-xs-block">

            <div class="col-sm-6 col-xs-12">
              <p class="text-center">
                <strong>Perspectiva evolución {{etiqueta[0]}}</strong>
              </p>
              <canvas id="languagePie" ></canvas>
            </div>
          
          </div>
        </div>
      </div>
    </div>
    <!-- /.row -->

  </section>
  <!-- /.content -->
</template>

<script>
import { db2 } from '../../main'
import Chart from 'chart.js'
/* import Chart from 'chart.js'
import $ from 'jquery'
import 'datatables.net'
import 'datatables.net-bs' */

export default {
  created () {
    db2.ref('/EvoluciónAcciones/Bancos').once('value', snapshot => this.cargarPerfiles(snapshot.val()))
    db2.ref('/PerspectivaBancos').once('value', snapshot => this.cargarPerspectivas(snapshot.val()))
  },
  data () {
    return {
      etiqueta: []
    }
  },
  methods: {
    cargarPerspectivas(perspectivas) {
/*       for (let i in perspectivas) {
        console.info(perspectivas[i])
      } */
      var datosC = Object.values(perspectivas[0])
      var etiquetas = Object.keys(perspectivas[0])
      etiquetas.splice(0, 1)
      etiquetas.splice(3, 3)
      this.etiqueta = datosC.splice(0, 1)
      datosC.splice(3, 3)

      var pieChartCanvas = document.getElementById('languagePie').getContext('2d')
      var pieConfig = {
        type: 'pie',
        data: {
          labels: etiquetas,
          datasets: [{
            data: datosC,
            backgroundColor: ['#D50000', '#ECE107', '#6BD500'],
            hoverBackgroundColor: ['#D50000', '#ECE107', '#6BD500']
          }]
        },
        options: {
          responsive: true,
          maintainAspectRatio: !this.isMobile,
          legend: {
            position: 'bottom',
            display: true
          }
        }
      }

      new Chart(pieChartCanvas, pieConfig) // eslint-disable-line no-new
    },
    cargarPerfiles(perfiles) {
      var listaFechas = []
      var listaPrecios = []
      var fechas = []
      var precios = []
      var strEx = null

      for (let i in perfiles) {
        for (let j in perfiles[i]) {
          fechas.push(perfiles[i][j].Fecha)
          strEx = perfiles[i][j].Price.replace(',', '.')
          precios.push(strEx)
          /* console.info(perfiles[i][j].Fechas) */
        }
        listaPrecios.push(precios.map(Number))
        precios = []
        listaFechas.push(fechas)
        fechas = []
      }
/*       for (let key in perfiles) {
        console.info(perfiles[key])
        var paco = perfiles[key]
        for (let i in perfiles[key]) {
          console.info(perfiles[i])
        }
      } */
      var ctx = document.getElementById('trafficBar').getContext('2d')
      var config = {
        type: 'line',
        data: {
          labels: listaFechas[0].reverse(),
          datasets: [{
            label: 'BBVA',
            fill: false,
            borderColor: '#284184',
            backgroundColor: 'rgba(0, 0, 0, 0)',
            data: listaPrecios[0].reverse(),
            pointRadius: 0,
            borderWidth: 1
          }, {
            label: 'Bankinter',
            fill: false,
            borderColor: '#4BC0C0',
            backgroundColor: 'rgba(0, 0, 0, 0)',
            data: listaPrecios[2].reverse(),
            pointRadius: 0,
            borderWidth: 1
          }, {
            label: 'CaixaBank',
            fill: false,
            borderColor: '#00a65a',
            backgroundColor: 'rgba(0, 0, 0, 0)',
            data: listaPrecios[3].reverse(),
            pointRadius: 0,
            borderWidth: 1
          }, {
            label: 'Sabadell',
            fill: false,
            borderColor: '#f39c12',
            backgroundColor: 'rgba(0, 0, 0, 0)',
            data: listaPrecios[4].reverse(),
            pointRadius: 0,
            borderWidth: 1
          }, {
            label: 'Santander',
            fill: false,
            borderColor: '#00c0ef',
            backgroundColor: 'rgba(0, 0, 0, 0)',
            data: listaPrecios[5].reverse(),
            pointRadius: 0,
            borderWidth: 1
          }
          ]
        },
        options: {
          responsive: true,
          maintainAspectRatio: !this.isMobile,
          legend: {
            position: 'bottom',
            display: true
          },
          tooltips: {
            mode: 'label',
            xPadding: 10,
            yPadding: 10,
            bodySpacing: 10
          }
        }
      }
      new Chart(ctx, config) // eslint-disable-line no-new
      // Termina
    }
  }
}
</script>
<style>
/* @import url('/static/js/plugins/datatables/jquery.dataTables.min.css'); */

.info-box.info-box-sm {
  min-height: 32px;
  font-size: 15px;
}

.info-box.info-box-sm small {
  font-size: 12px;
}

.info-box.info-box-sm .info-box-icon {
  height: 32px;
  width: 52px;
  font-size: 30px;
  line-height: 32px;
}

.info-box.info-box-sm .info-box-content {
  text-align: left;
  vertical-align: middle;
  display: inherit;
}

.info-box .info-box-content .info-box-text {
  font-size: 15px!important;
}

.info-box .info-box-content .info-box-number {
  font-size: 15px!important;
}

.fullCanvas {
  width: 100%;
}
</style>



