<template>
  <!-- Main content -->
  <section class="content">
    <!-- Info boxes -->
    <div class="row">
      <div class="clearfix visible-sm-block"></div>
        <div class="col-md-3 col-sm-6 col-xs-12">
          <div class="info-box info-box-sm bg-aqua">
            <span class="info-box-icon"><i class="fa fa-university"></i></span>
            <div class="info-box-content">
              <router-link to="/financieroIndices">
                <a>
                  <span class="info-box-text">Indices</span>
                </a>
              </router-link>
            </div>
            <!-- /.info-box-content -->
          </div>
        </div>
        <!-- /.col -->
        <div class="col-md-3 col-sm-6 col-xs-12">
          <div class="info-box info-box-sm bg-aqua">
            <span class="info-box-icon"><i class="fa fa-line-chart"></i></span>
            <div class="info-box-content">
              <router-link to="/financieroAcciones">
                <a>
                  <span class="info-box-text">Acciones</span>
                </a>
              </router-link>
            </div>
            <!-- /.info-box-content -->
          </div>
        </div>

        <div class="col-md-3 col-sm-6 col-xs-12">
          <div class="info-box info-box-sm bg-yellow">
            <span class="info-box-icon"><i class="fa fa-users"></i></span>
            <div class="info-box-content">
              <span class="info-box-text">Fondos</span>
            </div>
            <!-- /.info-box-content -->
          </div>
        </div>

        <div class="col-md-3 col-sm-6 col-xs-12">
          <div class="info-box info-box-sm bg-aqua">
            <span class="info-box-icon"><i class="fa fa-percent"></i></span>
            <div class="info-box-content">
              <span class="info-box-text">Ratios</span>
            </div>
            <!-- /.info-box-content -->
          </div>
        </div>
    </div>
    <!-- /.row -->

    <div class="col-xs-12">
      <div class="box">
        <div class="box-header with-border">
          <h3 class="box-title"></h3>
          <div class="box-body">

            <div class="col-sm-6 col-xs-12">
              <p class="text-center">
                <strong>FONDOS {{ this.etiqueta[0] }}</strong>
              </p>
              <canvas id="languagePie" ></canvas>
            </div>

            <hr class="visible-xs-block">

            <div class="col-sm-6 col-xs-12">
              <p class="text-center">
                <strong>FONDOS</strong>
              </p>
              <table id="ejemplo" class="display"></table>
            </div>
          
          </div>
        </div>
      </div>
    </div>
    <!-- /.row -->

  </section>
  <!-- /.content -->
</template>

<script>
import { db2 } from '../../main'
import Chart from 'chart.js'
import $ from 'jquery'
import 'datatables.net'
import 'datatables.net-bs'

export default {
  created () {
    db2.ref('/Composición F').once('value', snapshot => this.cargarPerfiles(snapshot.val()))
  },
  data () {
    return {
      etiqueta: []
    }
  },
  methods: {
    cargarPerfiles(perfiles) {
/*       this.etiquetas = []
      for (let key in perfiles) {
        this.etiquetas.push(perfiles[key].Nombre)
        console.info(Object.keys(perfiles[key]))
      } */
      var etiquetas = Object.keys(perfiles[0])
      etiquetas.splice(2, 1)
      var datosC = Object.values(perfiles[0])
      this.etiqueta = datosC.splice(2, 1)
      var datosFinal = []
      var strEx = null

      for (let key in datosC) {
        strEx = datosC[key].replace(',', '.')
        datosFinal.push(strEx)
      }
      datosFinal = datosFinal.map(Number)

      var tablaDatos = []
      for (let i in perfiles) {
        tablaDatos.push(Object.values(perfiles[i]))
      }
      tablaDatos.pop()
      $('#ejemplo').DataTable({
        data: tablaDatos,
        columns: [
          {title: 'E,G & P'}, {title: 'Financiero'}, {title: 'Nombre'}, {title: 'Otros'}, {title: 'Telecom'}
        ]
      })

      var pieChartCanvas = document.getElementById('languagePie').getContext('2d')
      var pieConfig = {
        type: 'doughnut',
        data: {
          labels: etiquetas,
          datasets: [{
            data: datosFinal,
            backgroundColor: ['#00a65a', '#f39c12', '#00c0ef', '#284184'],
            hoverBackgroundColor: ['#00a65a', '#f39c12', '#00c0ef', '#284184']
          }]
        },
        options: {
          responsive: true,
          maintainAspectRatio: !this.isMobile,
          legend: {
            position: 'bottom',
            display: true
          }
        }
      }

      new Chart(pieChartCanvas, pieConfig) // eslint-disable-line no-new
    }
  }
}
</script>
<style>
/* @import url('/static/js/plugins/datatables/jquery.dataTables.min.css'); */

.info-box.info-box-sm {
  min-height: 32px;
  font-size: 15px;
}

.info-box.info-box-sm small {
  font-size: 12px;
}

.info-box.info-box-sm .info-box-icon {
  height: 32px;
  width: 52px;
  font-size: 30px;
  line-height: 32px;
}

.info-box.info-box-sm .info-box-content {
  text-align: left;
  vertical-align: middle;
  display: inherit;
}

.info-box .info-box-content .info-box-text {
  font-size: 15px!important;
}

.info-box .info-box-content .info-box-number {
  font-size: 15px!important;
}

.fullCanvas {
  width: 100%;
}
</style>



