import DashView from './components/Dash.vue'
import NotFoundView from './components/404.vue'

// Import Views - Dash
import DashboardView from './components/views/Dashboard.vue'
import TablesView from './components/views/Tables.vue'
import TasksView from './components/views/Tasks.vue'
import Home from './components/views/Home.vue'
import Financiero from './components/views/Financiero.vue'
import Telecom from './components/views/Telecom.vue'
import FinancieroIndices from './components/views/FinancieroIndices.vue'
import FinancieroAcciones from './components/views/FinancieroAcciones.vue'
import FinancieroFondos from './components/views/FinancieroFondos.vue'

// Routes
const routes = [
  {
    path: '/',
    component: DashView,
    children: [
      {
        path: 'home',
        alias: '',
        component: Home,
        name: 'Home'
      },
      {
        path: 'financiero',
        alias: '',
        component: Financiero,
        name: 'Financiero'
      },
      {
        path: 'financieroIndices',
        alias: '',
        component: FinancieroIndices,
        name: 'Financiero > Indices'
      },
      {
        path: 'financieroAcciones',
        alias: '',
        component: FinancieroAcciones,
        name: 'Financiero > Acciones'
      },
      {
        path: 'financieroFondos',
        alias: '',
        component: FinancieroFondos,
        name: 'Financiero > Fondos'
      },
      {
        path: 'telecom',
        alias: '',
        component: Telecom,
        name: 'Zona de pruebas'
      },
      {
        path: 'dashboard',
        alias: '',
        component: DashboardView,
        name: 'Dashboard',
        meta: {description: 'Overview of environment'}
      }, {
        path: 'tables',
        component: TablesView,
        name: 'Tables',
        meta: {description: 'Simple and advance table in CoPilot'}
      }, {
        path: 'tasks',
        component: TasksView,
        name: 'Tasks',
        meta: {description: 'Tasks page in the form of a timeline'}
      }
    ]
  }, {
    // not found handler
    path: '*',
    component: NotFoundView
  }
]

export default routes



